/* Remove duplicated floating elements styles - they are now defined later in the file */

/* Features Section with Kaleidoscope Effects */
.features {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: 2rem;
  margin: 4rem 0;
  position: relative;
  z-index: 2;
}

/* Enhanced Hero Layout */
.hero-container {
  position: relative;
  z-index: 2;
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
}

.hero-layout {
  display: grid;
  grid-template-columns: 300px 1fr 400px;
  gap: 3rem;
  align-items: center;
  width: 100%;
  max-width: 1400px;
  margin: 0 auto;
  min-height: 600px;
}

.hero-left {
  display: flex;
  justify-content: center;
  align-items: center;
}

.kaleidoscope-wrapper {
  position: relative;
  animation: floatAnimation 6s ease-in-out infinite;
}

@keyframes floatAnimation {
  0%, 100% { transform: translateY(0) scale(1); }
  50% { transform: translateY(-20px) scale(1.05); }
}

.kaleidoscope-main {
  width: 250px;
  height: 250px;
  filter: drop-shadow(0 0 50px rgba(139, 92, 246, 0.5));
}

.outer-petals {
  animation: rotateSlow 30s linear infinite;
  transform-origin: 200px 200px;
}

.middle-shapes {
  animation: rotateReverse 20s linear infinite;
  transform-origin: 200px 200px;
}

.inner-triangles {
  animation: rotateSlow 15s linear infinite;
  transform-origin: 200px 200px;
}

@keyframes rotateSlow {
  from { transform: rotate(0deg); }
  to { transform: rotate(360deg); }
}

@keyframes rotateReverse {
  from { transform: rotate(360deg); }
  to { transform: rotate(0deg); }
}

.accent-shapes {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 100%;
  height: 100%;
  pointer-events: none;
}

.accent-shape {
  position: absolute;
  border-radius: 50%;
  background: radial-gradient(circle, rgba(139, 92, 246, 0.3) 0%, transparent 70%);
}

.accent-shape.shape-1 {
  width: 60px;
  height: 60px;
  top: -20px;
  right: -20px;
  animation: floatAccent 4s ease-in-out infinite;
}

.accent-shape.shape-2 {
  width: 40px;
  height: 40px;
  bottom: -10px;
  left: -10px;
  animation: floatAccent 5s ease-in-out infinite 1s;
}

.accent-shape.shape-3 {
  width: 80px;
  height: 80px;
  top: 50%;
  right: -40px;
  animation: floatAccent 6s ease-in-out infinite 2s;
}

@keyframes floatAccent {
  0%, 100% { transform: translate(0, 0) scale(1); opacity: 0.5; }
  50% { transform: translate(-10px, -10px) scale(1.2); opacity: 0.8; }
}

/* Hero Center Section */
.hero-center {
  text-align: center;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 0.5rem;
}

/* Hero Right Section */
.hero-right {
  display: flex;
  flex-direction: column;
  gap: 2rem;
  align-items: flex-start;
}

.hero-badge {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  gap: 0.5rem;
  background: rgba(255, 255, 255, 0.1);
  backdrop-filter: blur(10px);
  padding: 0.75rem 1.5rem;
  border-radius: 50px;
  font-size: 0.95rem;
  color: white;
  border: 1px solid rgba(255, 255, 255, 0.2);
  font-weight: 500;
  margin: 0;
}

.hero-title {
  font-weight: 900;
  margin: 0;
  line-height: 0.75;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 0;
  text-transform: uppercase;
}

.title-main {
  font-size: clamp(4rem, 10vw, 8.5rem);
  color: white;
  text-shadow: 0 6px 30px rgba(0, 0, 0, 0.3);
  text-align: center;
  width: 100%;
  margin: 0;
  display: block;
  letter-spacing: -0.02em;
  font-weight: 800;
}

/* Event Name - Rêveria */
.event-name {
  margin: 1rem 0 0.5rem 0;
  position: relative;
  display: inline-block;
}

.event-name-text {
  font-size: clamp(3.5rem, 7vw, 6rem);
  font-family: 'Inter', sans-serif;
  font-weight: 300;
  letter-spacing: 0.35em;
  background: linear-gradient(135deg, #EC4899, #8B5CF6, #F59E0B);
  background-size: 200% 200%;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
  text-transform: uppercase;
  position: relative;
  display: inline-block;
  animation: shimmerText 4s ease-in-out infinite;
  filter: drop-shadow(0 4px 20px rgba(236, 72, 153, 0.4));
}

@keyframes shimmerText {
  0%, 100% { 
    background-position: 0% 50%;
    filter: brightness(1);
  }
  50% { 
    background-position: 100% 50%;
    filter: brightness(1.2);
  }
}

.event-name-decoration {
  position: absolute;
  bottom: -15px;
  left: 50%;
  transform: translateX(-50%);
  width: 100%;
  height: 3px;
  background: linear-gradient(90deg, transparent, #EC4899, #8B5CF6, transparent);
  opacity: 0.9;
  animation: decorationSlide 3s ease-in-out infinite;
  border-radius: 2px;
  filter: drop-shadow(0 2px 10px rgba(236, 72, 153, 0.6));
}

@keyframes decorationSlide {
  0%, 100% { 
    width: 80%;
    opacity: 0.4;
  }
  50% { 
    width: 120%;
    opacity: 0.8;
  }
}

.title-year-container {
