.about-content {
  display: grid;
  gap: 2rem;
}

.about-card {
  background: white;
  padding: 2rem;
  border-radius: 20px;
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.1);
  transition: all 0.3s ease;
}

.about-card:hover {
  transform: translateY(-5px);
  box-shadow: 0 15px 40px rgba(0, 0, 0, 0.15);
}

.card-icon {
  width: 60px;
  height: 60px;
  margin-bottom: 1.5rem;
}

.icon-kaleidoscope {
  animation: rotateSlow 10s linear infinite;
}

.about-card h3 {
  font-size: 1.5rem;
  color: var(--text-dark);
  margin-bottom: 1rem;
}

.about-card p {
  color: var(--text-light);
  line-height: 1.8;
}

.about-visual {
  display: flex;
  justify-content: center;
  align-items: center;
}

.visual-container {
  position: relative;
  max-width: 400px;
  width: 100%;
}

.visual-image {
  width: 100%;
  height: auto;
  border-radius: 20px;
  filter: drop-shadow(0 20px 40px rgba(139, 92, 246, 0.2));
}

.visual-overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: linear-gradient(135deg, rgba(139, 92, 246, 0.1), rgba(236, 72, 153, 0.1));
  border-radius: 20px;
  pointer-events: none;
}

/* Vision Section */
.vision-section {
  background: var(--bg-light);
  padding: 5rem 0;
}

.vision-wrapper {
  max-width: 1200px;
  margin: 0 auto;
}

.vision-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: 2rem;
  margin-bottom: 3rem;
}

.vision-card {
  background: white;
  padding: 2.5rem;
  border-radius: 20px;
  text-align: center;
  box-shadow: 0 5px 20px rgba(0, 0, 0, 0.08);
  transition: all 0.3s ease;
}

.vision-card:hover {
  transform: translateY(-5px);
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.12);
}

.vision-icon {
  font-size: 3rem;
  margin-bottom: 1.5rem;
  display: inline-block;
  animation: bounce 3s ease-in-out infinite;
  animation-delay: calc(var(--i) * 0.2s);
}

.vision-card:nth-child(1) .vision-icon { --i: 0; }
.vision-card:nth-child(2) .vision-icon { --i: 1; }
.vision-card:nth-child(3) .vision-icon { --i: 2; }

.vision-card h3 {
  font-size: 1.5rem;
  color: var(--text-dark);
  margin-bottom: 1rem;
}

.vision-card p {
  color: var(--text-light);
  line-height: 1.8;
}

.vision-highlight {
  background: linear-gradient(135deg, rgba(139, 92, 246, 0.05), rgba(236, 72, 153, 0.05));
  padding: 3rem;
  border-radius: 20px;
  text-align: center;
  border: 2px solid rgba(139, 92, 246, 0.1);
}

.highlight-content {
  max-width: 800px;
  margin: 0 auto;
}

.highlight-icon {
  margin-bottom: 2rem;
}

.highlight-icon svg {
  width: 100px;
  height: 100px;
}

blockquote {
  margin: 0;
}

blockquote p {
  font-size: 1.2rem;
  line-height: 1.8;
  color: var(--text-dark);
  font-style: italic;
}

/* Enhanced Contact Section */
.contact-wrapper {
  max-width: 1000px;
  margin: 0 auto;
}

.word-animate {
  display: inline-block;
  animation: wordPop 0.6s ease-out;
  animation-fill-mode: both;
}

.word-animate:nth-child(1) { animation-delay: 0s; }
.word-animate:nth-child(2) { animation-delay: 0.1s; }
.word-animate:nth-child(3) { animation-delay: 0.2s; }

@keyframes wordPop {
  0% { opacity: 0; transform: translateY(20px) scale(0.9); }
  100% { opacity: 1; transform: translateY(0) scale(1); }
}

.card-inner {
  position: relative;
  height: 100%;
}

.contact-icon-wrapper {
  width: 80px;
  height: 80px;
  margin: 0 auto 1.5rem;
  position: relative;
}

.social-links-compact {
  display: flex;
  flex-direction: column;
  gap: 0.75rem;
}

.social-link-icon {
  padding: 0.75rem 1.25rem;
  background: rgba(139, 92, 246, 0.1);
  border-radius: 12px;
  color: var(--primary-purple);
  text-decoration: none;
  font-weight: 500;
  transition: all 0.3s ease;
  text-align: center;
}

.social-link-icon:hover {
  background: var(--primary-purple);
  color: white;
  transform: translateX(5px);
}

/* Footer Styles */
.footer {
  background: #1F2937;
  color: white;
  padding: 4rem 0 2rem;
  position: relative;
  overflow: hidden;
}

.footer-kaleidoscope-bg {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: radial-gradient(circle at 20% 50%, rgba(139, 92, 246, 0.1) 0%, transparent 50%),
              radial-gradient(circle at 80% 50%, rgba(236, 72, 153, 0.1) 0%, transparent 50%);
  pointer-events: none;
}

.footer-content {
  position: relative;
  z-index: 2;
}

.footer-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  gap: 3rem;
  margin-bottom: 3rem;
}

.footer-section h4 {
  font-size: 1.2rem;
  margin-bottom: 1.5rem;
  color: white;
}

.footer-logo {
  display: flex;
  align-items: center;
  gap: 1rem;
  margin-bottom: 1.5rem;
}

.footer-logo-icon {
  width: 40px;
  height: 40px;
}

.footer-kaleidoscope {
  animation: rotateSlow 10s linear infinite;
}

.footer-logo-title {
  font-size: 1.5rem;
  font-weight: 800;
  margin: 0;
}

.footer-logo-subtitle {
  font-size: 0.9rem;
  opacity: 0.8;
  margin: 0;
}

.footer-description {
  opacity: 0.8;
  line-height: 1.6;
}

.footer-links {
  list-style: none;
  padding: 0;
  margin: 0;
}

.footer-links li {
  margin-bottom: 0.75rem;
}

.footer-links a {
  color: rgba(255, 255, 255, 0.8);
  text-decoration: none;
  transition: all 0.3s ease;
}

.footer-links a:hover {
  color: white;
  transform: translateX(5px);
}

.footer-social {
  display: flex;
  gap: 1rem;
  margin-bottom: 1.5rem;
}

.social-icon {
  width: 40px;
  height: 40px;
  background: rgba(255, 255, 255, 0.1);
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: all 0.3s ease;
}

.social-icon svg {
  width: 20px;
  height: 20px;
  fill: white;
}

.social-icon:hover {
  background: var(--primary-purple);
  transform: translateY(-3px);
}

.footer-email {
  color: rgba(255, 255, 255, 0.8);
  text-decoration: none;
  transition: color 0.3s ease;
}

.footer-email:hover {
  color: white;
}

.footer-text {
  opacity: 0.8;
  margin-bottom: 1rem;
}

.footer-cta {
  display: inline-block;
  padding: 0.75rem 1.5rem;
  background: var(--primary-purple);
  color: white;
  text-decoration: none;
  border-radius: 50px;
  font-weight: 600;
  transition: all 0.3s ease;
}

.footer-cta:hover {
  background: var(--primary-pink);
  transform: translateY(-2px);
}

.footer-bottom {
  padding-top: 2rem;
}

.footer-divider {
  height: 1px;
  background: linear-gradient(90deg, transparent, rgba(255, 255, 255, 0.2), transparent);
  margin-bottom: 2rem;
}

.footer-bottom-content {
  text-align: center;
  opacity: 0.6;
}

.footer-bottom-content p {
  margin: 0.5rem 0;
  font-size: 0.9rem;
}

.footer-credits {
  font-size: 0.85rem;
}

/* Floating Elements */
.floating-element {
  position: absolute;
  border-radius: 50%;
  background: radial-gradient(circle, rgba(139, 92, 246, 0.2) 0%, transparent 70%);
  pointer-events: none;
}

.element-1 {
  width: 300px;
  height: 300px;
  top: 10%;
  left: -150px;
  animation: floatElement 20s ease-in-out infinite;
}

.element-2 {
  width: 200px;
  height: 200px;
  top: 50%;
  right: -100px;
  animation: floatElement 25s ease-in-out infinite 5s;
}

.element-3 {
  width: 150px;
  height: 150px;
  bottom: 20%;
  left: 10%;
  animation: floatElement 18s ease-in-out infinite 10s;
}

.element-4 {
  width: 250px;
  height: 250px;
  top: 30%;
  right: 20%;
  animation: floatElement 22s ease-in-out infinite 15s;
  opacity: 0.3;
}

.element-5 {
  width: 180px;
  height: 180px;
  bottom: 10%;
  right: 30%;
  animation: floatElement 20s ease-in-out infinite 7s;
}

@keyframes floatElement {
  0%, 100% {
    transform: translate(0, 0) scale(1);
  }
  25% {
    transform: translate(50px, -30px) scale(1.1);
  }
  50% {
    transform: translate(-30px, 50px) scale(0.9);
  }
  75% {
    transform: translate(30px, 30px) scale(1.05);
  }
}

/* Mobile Responsive Updates */
@media (max-width: 768px) {
  .mobile-menu-toggle {
    display: flex;
  }
  
  .nav-links {
    position: fixed;
    top: 0;
    left: -100%;
