    height: 100vh;
    background: rgba(30, 41, 59, 0.98);
    backdrop-filter: blur(10px);
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 2rem;
    transition: left 0.3s ease;
  }
  
  .nav-links.mobile-open {
    left: 0;
  }
  
  .header.scrolled .mobile-menu-toggle span {
    background: var(--text-dark);
  }
  
     .hero-layout {
     grid-template-columns: 1fr;
     gap: 2rem;
     text-align: center;
     padding: 0 1rem;
   }
   
   .hero-left {
     order: 1;
     margin-bottom: 0;
   }
   
   .hero-center {
     order: 0;
     margin-bottom: 1rem;
   }
   
   .hero-right {
     order: 2;
     align-items: center;
     text-align: center;
   }
   
   .kaleidoscope-main {
     width: 200px;
     height: 200px;
   }
   
   .hero-badge {
     font-size: 0.85rem;
     padding: 0.6rem 1.2rem;
   }
   
   .title-main {
     font-size: clamp(3rem, 12vw, 5rem);
     letter-spacing: -0.01em;
   }
   
   .year-number {
     font-size: clamp(4rem, 18vw, 7rem);
   }
   
   .event-name-text {
     font-size: clamp(2.5rem, 10vw, 3.5rem);
     letter-spacing: 0.2em;
   }
   
   .divider-text {
     font-size: 1rem;
     letter-spacing: 0.2em;
   }
   
   .divider-icon {
     font-size: 0.9rem;
   }
   
   .hero-description {
     text-align: center;
   }
   
   .hero-actions {
     justify-content: center;
     width: 100%;
   }
   
   .hero-stats {
     align-items: center;
   }
  
  .about-grid {
    grid-template-columns: 1fr;
    gap: 3rem;
  }
  
  .about-visual {
