  }
  
  .footer-social {
    justify-content: center;
  }
}

/* Animations on Scroll */
.reveal {
  opacity: 0;
  transform: translateY(30px);
  transition: all 0.8s ease;
}

.reveal.active {
  opacity: 1;
  transform: translateY(0);
}

/* Add reveal animation trigger */
@media (prefers-reduced-motion: no-preference) {
  .reveal {
    transition: opacity 0.8s ease, transform 0.8s ease;
  }
}

/* Registration Page Styles */
.registration-page {
  min-height: 100vh;
  background: #f5f5f5;
}

.registration-hero {
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  padding: 100px 0 60px;
  text-align: center;
  color: white;
}

.registration-title {
  margin-bottom: 1.5rem;
}

.title-line {
  display: block;
  font-size: clamp(2.5rem, 5vw, 3.5rem);
  font-weight: 900;
  margin-bottom: 0.5rem;
  text-shadow: 0 4px 20px rgba(0, 0, 0, 0.2);
}

.title-event {
  display: block;
  font-size: clamp(1.5rem, 3vw, 2rem);
  font-weight: 600;
  color: #FCD34D;
  text-shadow: 0 2px 10px rgba(0, 0, 0, 0.2);
}

.registration-subtitle {
  font-size: 1.2rem;
  opacity: 0.9;
  max-width: 600px;
  margin: 0 auto;
}

.registration-container {
  padding: 3rem 1rem;
  max-width: 800px;
}

.registration-content {
  background: white;
  border-radius: 20px;
  box-shadow: 0 10px 40px rgba(0, 0, 0, 0.1);
  padding: 3rem;
  position: relative;
  overflow: hidden;
}

.registration-content::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 5px;
  background: linear-gradient(90deg, #667eea, #764ba2, #EC4899);
}

/* Form Styles */
.registration-form {
  max-width: 600px;
  margin: 0 auto;
}

.form-group {
  margin-bottom: 2.5rem;
  position: relative;
}

.form-group label {
  display: block;
  font-size: 1.1rem;
  font-weight: 600;
  color: #1F2937;
  margin-bottom: 0.5rem;
}

.required {
  color: #EF4444;
  font-weight: 700;
}

.field-description {
  font-size: 0.9rem;
  color: #6B7280;
  margin-bottom: 1rem;
  line-height: 1.5;
}

.form-group input[type="text"],
.form-group input[type="email"],
.form-group input[type="tel"],
.form-group input[type="date"],
.form-group select {
  width: 100%;
  padding: 0.75rem 1rem;
  font-size: 1rem;
  border: none;
  border-bottom: 2px solid #E5E7EB;
  background: #F9FAFB;
  border-radius: 8px 8px 0 0;
  transition: all 0.3s ease;
  font-family: inherit;
}

.form-group input[type="text"]:focus,
.form-group input[type="email"]:focus,
.form-group input[type="tel"]:focus,
.form-group input[type="date"]:focus,
.form-group select:focus {
  outline: none;
  border-bottom-color: #8B5CF6;
  background: white;
  box-shadow: 0 4px 12px rgba(139, 92, 246, 0.1);
}

.form-group input.error,
.form-group select.error {
  border-bottom-color: #EF4444;
}

.error-message {
  display: block;
  color: #EF4444;
  font-size: 0.875rem;
  margin-top: 0.5rem;
  font-weight: 500;
}

/* Radio Group Styles */
.radio-group {
  display: flex;
  gap: 2rem;
  margin-top: 1rem;
}

.radio-label {
  display: flex;
  align-items: center;
  cursor: pointer;
  font-size: 1rem;
  color: #374151;
}

.radio-label input[type="radio"] {
  width: 20px;
  height: 20px;
  margin-right: 0.5rem;
  cursor: pointer;
  accent-color: #8B5CF6;
}

.radio-text {
  font-weight: 500;
}

/* Select Dropdown */
select {
  cursor: pointer;
  background-image: url("data:image/svg+xml;charset=UTF-8,%3csvg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 24 24' fill='none' stroke='currentColor' stroke-width='2' stroke-linecap='round' stroke-linejoin='round'%3e%3cpolyline points='6 9 12 15 18 9'%3e%3c/polyline%3e%3c/svg%3e");
  background-repeat: no-repeat;
  background-position: right 1rem center;
  background-size: 1.5em 1.5em;
  padding-right: 3rem;
  appearance: none;
}

/* Submit Button */
.form-actions {
  margin-top: 3rem;
  text-align: center;
}

.btn-submit {
  min-width: 200px;
  padding: 1rem 2.5rem;
  font-size: 1.1rem;
  font-weight: 600;
  letter-spacing: 0.05em;
  box-shadow: 0 4px 20px rgba(139, 92, 246, 0.3);
  display: inline-flex;
  align-items: center;
  justify-content: center;
  gap: 0.5rem;
}

.btn-submit:disabled {
  opacity: 0.7;
  cursor: not-allowed;
}

.spinner {
  width: 18px;
  height: 18px;
  border: 2px solid rgba(255, 255, 255, 0.3);
  border-top-color: white;
  border-radius: 50%;
  animation: spin 0.6s linear infinite;
}

@keyframes spin {
  to { transform: rotate(360deg); }
}

/* Success Message */
.success-message {
  text-align: center;
  padding: 3rem;
}

.success-icon {
  font-size: 4rem;
  display: block;
  margin-bottom: 1.5rem;
}

.success-message h3 {
  font-size: 2rem;
  color: #10B981;
  margin-bottom: 1rem;
}

.success-message p {
  font-size: 1.1rem;
  color: #6B7280;
  margin-bottom: 2rem;
}

.success-message .btn {
  margin: 0 auto;
}

/* Mobile Responsive */
@media (max-width: 768px) {
  .registration-hero {
    padding: 80px 0 40px;
  }
  
  .registration-container {
    padding: 2rem 1rem;
  }
  
  .registration-content {
    padding: 2rem 1.5rem;
  }
  
  .form-group {
    margin-bottom: 2rem;
  }
  
  .radio-group {
    flex-direction: column;
    gap: 1rem;
  }
  
  .btn-submit {
    width: 100%;
  }
} 